.ico-align {                  /* Блок рецептов */ 
    margin-left: -6px;        /* Сдвиг блока рецептов влево */
    padding-top: 6px;         /* Сдвиг блока рецептов вниз */ 
    padding-right: 10px;       /* Сдвиг текста рецептов вправо */
    vertical-align: top;      /* Выравнивание верхнего края элемента по верху самого высокого элемента строки */
    object-fit: cover;        /* Смещаемый контент меняет свой размер таким образом, чтобы сохранять свои пропорции при заполнении блока: окончательный размер контента будет определен как "покрытие" блока, ограничиваясь его шириной и высотой.*/
}

.ico-crop {
    width: 100px;
    height: 100px;
    border-radius: 10px;
    object-fit: cover;
}

.disable-selectionn {
    color:  #808080;
    margin-bottom: auto;
}

.text {
    display: -webkit-box;
}

.open-tovar {
    max-height: 300px;
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.text-open-tovar {
    text-align: center;
}

.money-open-tovar {
    text-align: center;
}

.category-open-tovar {
    text-align: center;
    color:  #808080;
}

.description-open-tovar {
    text-align: center;
}

.zapret-videleniya {
    -webkit-touch-callout: none; /* не дать выделять текст iOS Safari */
    -webkit-user-select: none;   /* не дать выделять текст Chrome/Safari/Opera */
    -khtml-user-select: none;    /* не дать выделять текст Konqueror */
    -moz-user-select: none;      /* не дать выделять текст Firefox */
    -ms-user-select: none;       /* не дать выделять текст Internet Explorer/Edge */
    user-select: none;           /* не дать выделять текст Non-prefixed version, currentl not supported by any browser */
}

